article {
    border-radius: 10px;
    width: 30%;
    height: 340px;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    position: relative;
    overflow: hidden;
    margin: 15px 1.66%;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    cursor: pointer;
  
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      border-radius: 10px;
      pointer-events: none;
      background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(0, 0, 0, 0.5) 100%);
    }
  
    &:hover {
      opacity: 0.9;
    }
  
    h3 {
      font-weight: 500;
      z-index: 10;
      position: absolute;
      bottom: 10px;
      left: 20px;
      right: 20px;
      margin: 0;
      font-size: 1.3em;
    }
  
    img {
      width: 100%;
      height: 340px;
      object-fit: cover;
      object-position: center;
    }
  
    @media (max-width: 992px) {
      width: 45%;
      margin: 10px;
      height: 300px;
  
      img {
        height: 300px;
      }
    }
  
    @media (max-width: 576px) {
      width: 100%;
      height: 255px;
      margin: 0;
  
      img {
        height: 255px;
      }
    }
  }
  